BACA,CAEA,+BACA,SACA,CAEA,gCAEA,iBAAA,CACA,eAAA,CAFA,UAGA,CAEA,sCACA,MACA,CAEA,+BACA,SACA,CACA","sourcesContent":["<template>\n  <div>\n    <!-- Bannière principale -->\n    <div class=\"banner\">\n      <div class=\"banner-text\">\n        <h1>Bienvenue sur Mon Portfolio</h1>\n        <a href=\"/portfolio\" class=\"banner-button\">Découvrir mon travail</a>\n      </div>\n    </div>\n\n    <!-- Section \"À propos de moi\" -->\n    <div class=\"about-section\">\n      <h1 class=\"section-title\">À propos de moi</h1>\n      <div class=\"about-content\">\n        <div class=\"about-image\">\n          <img src=\"/images/PhotoAdWebHomePage.jpg\" alt=\"Photo de profil\" />\n        </div>\n        <div class=\"about-text\">\n          <p v-html=\"propos[0].description\"></p>\n        </div>\n      </div>\n    </div>\n\n    <!-- Section \"Mes formations\" sous forme de frise chronologique -->\n    <div id=\"formations-section\">\n      <h1 class=\"section-title\">Mes formations</h1>\n      <div class=\"timeline\">\n        <div class=\"timeline-bar\" :style=\"{ height: barHeight + '%' }\"></div>\n\n        <div\n          v-for=\"(formation, index) in formations\"\n          :key=\"index\"\n          class=\"timeline-item\"\n          :class=\"{ 'left': index % 2 === 0, 'right': index % 2 !== 0 }\"\n        >\n          <div class=\"timeline-dot\"></div>\n          <div class=\"timeline-content\">\n            <h4>{{ formation.name }}</h4>\n            <p v-html=\"formation.description\"></p>\n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  props: {\n    propos: {\n      type: Array,\n      required: true,\n    },\n    formations: {\n      type: Array,\n      required: true,\n    },\n  },\n  data() {\n    return {\n      barHeight: 0, // Hauteur initiale de la barre (en %)\n    };\n  },\n  mounted() {\n    window.addEventListener(\"scroll\", this.updateProgressBar);\n  },\n  beforeUnmount() {\n    window.removeEventListener(\"scroll\", this.updateProgressBar);\n  },\n  methods: {\n    updateProgressBar() {\n      const section = document.getElementById(\"formations-section\");\n      if (!section) return;\n\n      const rect = section.getBoundingClientRect();\n      const windowHeight = window.innerHeight;\n      const scrollY = window.scrollY;\n      const sectionTop = rect.top + scrollY;\n      const sectionHeight = section.offsetHeight;\n\n      // Calculer le pourcentage de progression dans la section\n      let progress =\n        ((scrollY + windowHeight - sectionTop) / sectionHeight) * 100;\n      progress = Math.max(0, Math.min(100, progress)); // Garde la valeur entre 0 et 100\n      this.barHeight = progress;\n    },\n  },\n};\n</script>\n\n<style scoped>\n/* === BANNIÈRE === */\n.banner {\n  position: relative;\n  height: 100vh;\n  background-image: url('/images/banniere_portfolio.png');\n  background-size: cover;\n  background-attachment: fixed;\n  background-position: center;\n  display: flex;\n  justify-content: flex-end;\n  align-items: center;\n  text-align: right;\n  color: #fff;\n  padding: 50px;\n  animation: fadeIn 1.5s ease-in-out;\n}\n\n.banner-text h1 {\n  font-size: 4.5rem;\n  font-weight: 800;\n  letter-spacing: 3px;\n  text-shadow: 0 0 15px rgba(255, 255, 255, 0.9);\n}\n\n.banner-button {\n  padding: 15px 30px;\n  background: linear-gradient(135deg, #ff6b6b, #ff9f43);\n  color: #ffffff;\n  border: none;\n  border-radius: 30px;\n  font-size: 1.2rem;\n  cursor: pointer;\n  transition: all 0.3s ease;\n}\n\n.banner-button:hover {\n  background: linear-gradient(135deg, #ff4757, #ff6b6b);\n  transform: translateY(-5px);\n}\n\n/* === SECTION À PROPOS === */\n.about-section {\n  padding: 100px 20px;\n  background-color: #fff;\n  text-align: center;\n}\n\n.section-title {\n  font-size: 3rem;\n  font-weight: 700;\n  text-transform: uppercase;\n  color: #ff6b6b;\n  text-shadow: 0 0 10px rgba(255, 107, 107, 0.7);\n}\n\n.about-content {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  gap: 50px;\n}\n\n.about-image img {\n  width: 220px;\n  height: 220px;\n  border-radius: 50%;\n  object-fit: cover;\n  border: 5px solid #fff;\n  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.2);\n  transition: transform 0.4s ease;\n}\n\n.about-image img:hover {\n  transform: scale(1.1);\n}\n\n.about-text p {\n  font-size: 1.3rem;\n  color: #555;\n  font-weight: 400;\n  max-width: 700px;\n  line-height: 1.8;\n  margin-top: 20px;\n}\n\n/* === FRISE CHRONOLOGIQUE === */\n.timeline {\n  position: relative;\n  max-width: 1000px;\n  margin: 50px auto;\n  padding: 20px;\n}\n\n/* Barre de progression dynamique */\n.timeline-bar {\n  position: absolute;\n  width: 6px;\n  background: linear-gradient(180deg, #ff6b6b, #ff9f43);\n  top: 0;\n  left: 50%;\n  transform: translateX(-50%);\n  border-radius: 10px;\n  transition: height 2.0s ease-in-out;\n  height: 0;\n}\n\n/* === CONTENEUR DES FORMATIONS === */\n.timeline-item {\n  position: relative;\n  width: 50%;\n  padding: 20px 40px;\n  box-sizing: border-box;\n}\n\n.timeline-item.left {\n  left: 0;\n  text-align: right;\n}\n\n.timeline-item.right {\n  left: 50%;\n  text-align: left;\n}\n\n/* === BULLES DES FORMATIONS === */\n.timeline-dot {\n  position: absolute;\n  top: 20px;\n  width: 20px;\n  height: 20px;\n  background: #ff6b6b;\n  border-radius: 50%;\n  left: calc(50% - 10px);\n  border: 4px solid white;\n  box-shadow: 0 0 10px rgba(255, 107, 107, 0.5);\n}\n\n/* === CONTENU DES FORMATIONS === */\n.timeline-content {\n  background: rgba(255, 255, 255, 0.9);\n  padding: 20px;\n  border-radius: 8px;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  transition: all 0.3s ease;\n}\n\n.timeline-content:hover {\n  transform: translateY(-5px);\n  box-shadow: 0 6px 12px rgba(255, 0, 0, 0.3);\n}\n\n/* === RESPONSIVE DESIGN === */\n@media (max-width: 768px) {\n  .about-content {\n    flex-direction: column;\n  }\n\n  .timeline-bar {\n    left: 20px;\n  }\n\n  .timeline-item {\n    width: 100%;\n    padding-left: 50px;\n    text-align: left;\n  }\n\n  .timeline-item.right {\n    left: 0;\n  }\n\n  .timeline-dot {\n    left: 10px;\n  }\n}\n</style>\n"],"sourceRoot":""}]);
